<template>
  <div class="incharge-page">
    <van-nav-bar
      fixed
      border
      title="需求记录"
      left-arrow
      @click-left="goBack"
    />
	<van-pull-refresh
	  v-model="published.refreshing"
	  @refresh="onPublishedRefresh"
	>
	  <van-list
	    v-show="published.list.length > 0"
	    class="cell-group"
	    v-model="published.loading"
	    :finished="published.finished"
	    finished-text="没有更多了"
	    @load="loadMorePublished"
	  >
	    <demand-item
	      v-for="item in published.list"
	      :key="item.id"
	      :demand="item"
	      @click="handleDemandClick(item.id)"
	    />
	  </van-list>
	  <div class="no-content" v-if="published.list.length === 0">
	    <img src="../assets/no-content.png" alt="" />
	    <p><strong>暂无需求</strong></p>
	    <p>你还没发布过需求，快来发布吧~</p>
	  </div>
	</van-pull-refresh>
  </div>
</template>
<script>
import { mapGetters, mapState } from "vuex";
import {
  Button,
  Row,
  Col,
  NavBar,
  List,
  Cell,
  PullRefresh,
  Tabs,
  Tab
} from "vant";
import { getDemandByUserId, getDemandBySolverId } from "../api/demand";
import DemandItem from "../components/demand-item";

export default {
  name: "about",
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Row.name]: Row,
    [Col.name]: Col,
    [List.name]: List,
    [Cell.name]: Cell,
    [PullRefresh.name]: PullRefresh,
    [NavBar.name]: NavBar,
    [Button.name]: Button,
    DemandItem
  },
  data() {
    return {
      active: 0,
      published: {
        list: [],
        page: 1,
        limit: 20,
        loading: false,
        finished: false,
        refreshing: false
      },
      solved: {
        list: [],
        page: 1,
        limit: 20,
        loading: false,
        finished: false,
        refreshing: false
      }
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"]),
    tabName() {
      return this.$route.query.tab;
    }
  },
  mounted() {
    if (this.tabName === "publish") {
      this.active = 0;
    } else if (this.tabName === "solve") {
      this.active = 1;
    }
    this.onPublishedRefresh();
    this.onSolvedRefresh();
  },
  methods: {
    // 点击返回按钮
    goBack() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },
    async onPublishedRefresh() {
      this.published.page = 1;
      const list = await this.fetchPublishedList();
      this.published.list = list;
      this.published.refreshing = false;
	  console.log(this.published.list.length)
    },
    async loadMorePublished() {
      this.published.page = this.published.page + 1;
      const list = await this.fetchPublishedList();
      this.published.list = [...this.published.list, ...list];
      this.published.loading = false;
    },
    async fetchPublishedList() {
      const res = await getDemandByUserId({
        page: this.published.page,
        limit: this.published.limit,
        user_id: this.userInfo.id
      });
      this.published.finished = res.length < this.published.limit;
      return res;
    },
    async onSolvedRefresh() {
      this.solved.page = 1;
      const list = await this.fetchSolvedList();
      this.solved.list = list;
      this.solved.refreshing = false;
    },
    async loadMoreSolved() {
      this.solved.page = this.solved.page + 1;
      const list = await this.fetchSolvedList();
      this.solved.list = [...this.solved.list, ...list];
      this.solved.loading = false;
    },
    async fetchSolvedList() {
      const res = await getDemandBySolverId({
        page: this.solved.page,
        limit: this.solved.limit,
        solve_user_id: this.userInfo.id
      });
      this.solved.finished = res.length < this.solved.limit;
      return res;
    },
    handleDemandClick(id) {
      if (this.userInfo.status !== "2") {
        this.$dialog
          .confirm({
            title: "温馨提示",
            message: "您需要注册后才能进行该操作哦",
            confirmButtonText: "马上注册",
            cancelButtonText: "稍后再说"
          })
          .then(() => {
            if (this.userInfo.is_subscribe === "1") {
              this.$router.push("/my-card");
            } else {
              // this.$router.replace("/follow-us");
			  console.log("未注册用户")
            }
          })
          .catch(() => {
            // on cancel
            console.log("cancel dialog");
          });
      } else {
        if (this.userInfo.is_subscribe === "1") {
          this.$router.push(`/demands/${id}`);
        } else {
          // this.$router.replace("/follow-us");
		  console.log("未注册用户")
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
.cell-group {
  margin-top: 12px;
}

.no-content {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 50px 20px;

  p {
    font-size: 14px;
    margin: 0;
    color: #999;
    line-height: 2em;
    strong {
      color: #000;
      font-size: 1.2em;
    }
  }

  img {
    width: 120px;
    margin-bottom: 30px;
  }
}

@media (min-width: 750px) {
  .cell-group {
    margin-top: 12px;
  }

  .no-content {
    padding: 50px 20px;

    p {
      font-size: 14px;
    }

    img {
      width: 120px;
      margin-bottom: 30px;
    }
  }
}
</style>
